import React, { useState, useContext } from "react";

import { Collapse, Button, CardBody, Card } from 'reactstrap';

import { ScenarioContext } from "../contexts/ScenarioContext";
import { usePrefs } from "../contexts/PreferencesContext"

import classnames from "classnames";

import CheckListItem from "./CheckListItem"

const isLeafNode = (step) => {  // No children
  return !step.children || step.children.length <= 0
}

const isBranchNode = (step) => {  // Has at least one child and all children are leaves
  if (isLeafNode(step)) {  // No children
    return false;
  }
  const leafArr = step.children.map((childStep) => (isLeafNode(childStep)));
  return leafArr.every(leaf => leaf);
}

const isLimbNode = (step) => {  // All children are branches
  const branchArr = step.children.map((childStep) => (isBranchNode(childStep)));
  return branchArr.every(branch => branch);
}

export default function CheckList({ steps, heading, depth = 3, first = false}){
  const { setCollapsedValue, toggleCollapsed, getCollapsed, setCurrentNode } = useContext(ScenarioContext)
  const { dispLeafNodesInline } = usePrefs()
  
  const getCheckListRaterInfoLayout = () => {
    return (
      <ul className={classnames({first:  first})}>
        {steps.map((step) => (
          <li key={`li-${step.id}`}>
            {isLeafNode(step)
            ?
              <CheckListItem step={step} />
            :
              <>
                <p
                  style={{cursor: "pointer"}}
                  className={`h${depth}`}
                  onClick={() => toggleCollapsed(step.id)}>
                    {step.label}
                    <span className="material-icons-outlined">{getCollapsed(step.id) ? "expand_more" : "chevron_right"}</span>
                </p>

                <Collapse isOpen={getCollapsed(step.id)}>
                  <CheckList
                      steps={step.children}
                      depth={depth + 1}
                  />
                </Collapse>
              </>
            }
          </li>
        )
        )}
      </ul>
    )
  }

  const getNavigationDetailsLayout = () => {
    return (
      <ul className={first ? "first" : ""}>
      {steps.map((step) => (
          <li key={step.id}>
          {
              (isLimbNode(step))
              ?
                  <>
                      <p
                        style={{cursor: "pointer"}}
                        className={`h${depth + 1}`}
                        onClick={() => toggleCollapsed(step.id)}>
                          {step.label}
                          <span className="material-icons-outlined">{getCollapsed(step.id) ? "expand_more" : "chevron_right"}</span>
                      </p>
                      <Collapse isOpen={getCollapsed(step.id)}>
                        <CheckList
                            steps={step.children}
                            depth={depth + 1}
                        />
                      </Collapse>
                  </>
              :
                  <p
                    style={{cursor: "pointer"}}
                    onClick={() => setCurrentNode(step.id)}
                    className={`h${depth + 1}`}>
                          {step.label}
                  </p>
          }
          </li>

      ))}
      </ul>
    )
  }


  return (
    <>
      { dispLeafNodesInline ? getCheckListRaterInfoLayout() : getNavigationDetailsLayout() }
    </>
  )

}
